@charset "utf-8";

/*** main ***/
.loading-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.movie-list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}

.movie-list li {
  flex: 0 0 calc(50% - 20px);
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
}

.movie-list .img-box {
  width: 30%;
  transform: translateY(-40px);
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.movie-list img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.movie-list .txt-box {
  position: relative;
  width: calc(70% - 20px);
  padding: 20px;
  box-sizing: border-box;
}


.movie-list .title {
  margin-bottom: 10px;
  font-size: 16px;
}

.movie-list p~p {
  margin-top: 5px;
}

.movie-list .rating {
  position: absolute;
  bottom: 0;
  right: 0;
  color: red;
  font-weight: bold;
  font-size: 14px;
}

.movie-list .m-btn {
  display: inline-block;
  margin-top: 20px;
  padding: 5px 10px;
  background: #333;
  color: #fff;
}

/* hover */
.movie-list li:hover .img-box {
  transform: translateY(-50px);
}

/*** main - detail ***/
.sec-detail {
  display: flex;
  justify-content: space-between;
}

.sec-detail .img-box {
  width: 30%;
}

.sec-detail img {
  width: 100%;
}

.sec-detail .txt-box {
  width: calc(70% - 20px);
}

.sec-detail .title {
  margin-bottom: 10px;
  font-size: 20px;
}

.sec-detail .genres {
  margin-bottom: 10px;
}

/* 가로해상도 1025px 이상 서식 */
@media screen and (min-width: 1025px) {}

/* 가로해상도 768px ~ 1024px 서식 */
@media screen and (max-width: 1024px) {
  .movie-list li {
    flex: 0 0 100%;
  }
}

/* 가로해상도 767px 이하 서식 */
@media screen and (max-width: 767px) {
  .movie-list li {
    display: block;
  }

  .movie-list .img-box {
    width: 50%;
    transform: translateY(-30px);
  }

  .movie-list .txt-box {
    width: 100%;
    padding: 0 0 30px;
  }

  /* hover */
  .movie-list li:hover .img-box {
    transform: translateY(-40px);
  }
}