@charset "utf-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

/* reset */
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 13px;
  color: #333;
  background-color: #eff3f7;
}

a {
  color: #333;
  text-decoration: none;
  font-size: 16px;
}

ol,
ul,
li {
  list-style: none;
}

/* 공통 */
.header {
  width: 200px;
  /* height: 100%; */
  height: calc(100% - 40px);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.header h1 {
  padding: 70px 0 50px;
  text-align: center;
}

/* nav */
.nav {
  padding-top: 70px;
  text-align: center;
}

.nav li {
  line-height: 40px;
}

.nav a {
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  color: #5d5d5d;
  text-shadow: 0 1px 1px #adadad;
}

/* main */
main {
  width: calc(100% - 200px);
  /* height: 100%; */
  height: calc(100% - 40px);
  position: fixed;
  left: 200px;
  top: 0;
  /* padding-top: 50px; */
  padding: 50px;
  /* overflow: scroll; */
  overflow: auto;
  box-sizing: border-box;
}

main section {
  padding: 10px 0;
}

/* footer */
.footer {
  width: 100%;
  line-height: 40px;
  background: #333;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 1000;
}

.footer address {
  color: #fff;
  text-align: center;
  font-style: normal;
}

/* 가로해상도 1025px 이상 서식 */
@media screen and (min-width: 1025px) {}

/* 가로해상도 768px ~ 1024px 서식 */
/* @media screen and (min-width: 768px) and (max-width: 1024px) {} */
@media screen and (max-width: 1024px) {
  .header {
    position: static;
    width: 100%;
    height: auto;
  }

  .header h1 {
    padding: 20px 0;
  }

  .nav {
    padding: 0;
  }

  .nav ul {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .nav li {
    flex: 1 1 auto;
  }

  main {
    position: static;
    width: 100%;
    height: auto;
  }

  .footer {
    position: static;
  }
}

/* 가로해상도 767px 이하 서식 */
@media screen and (max-width: 767px) {}